<template>
<!--  为了Association 页面中详细展示study。-->
<!--  因为其他详情页中的study是展示所有-->
<!--  association的汇总，所以不适合用在这里。-->
<!--  这里是要展示的每个study中的详细信息-->

  <v-sheet>

    <v-sheet class="pa-3 " min-height="600">
      <v-sheet>

        <legend-category></legend-category>
        <trait-type></trait-type>
        <delta-trend-summary></delta-trend-summary>
        <pval-summary></pval-summary>
        <legend-sex></legend-sex>
        <open-external-link></open-external-link>

      </v-sheet>
      <vxe-toolbar ref="xToolbar1S" custom export ></vxe-toolbar>
      <vxe-pager
        :current-page="tablePage1.currentPage"
        :layouts="['PrevPage', 'JumpNumber', 'NextPage', 'FullJump', 'Sizes', 'Total']"
        :loading="tableloading1"
        :page-size="tablePage1.pageSize"
        :total="dataLength"
        @page-change="handlePageChange1">
      </vxe-pager>
      <vxe-table
        ref="xTable1S"
        align="left"
        :column-config="{resizable: true}"
        :data="pagedData"
        :export-config="{filename:'Metabolite_'+cid}"
        :print-config="{}"
        :loading="tableloading1"
        border
        empty-text="Empty"
        round
        stripe
      >

        <vxe-column field="Cohort_id" title="Study" sortable width="100">
          <template #default="{row}" >
            <a style="text-decoration: none" class="font-weight-bold text-body-2"
               @click="$commonfunc.openAtNewPageStudyDetail(row.Cohort_id)"
            >{{row.Cohort_id}}</a>
          </template>

        </vxe-column>
        <vxe-column field="Type" min-width="60" title="Category">
          <template #default="{row}" >
            <v-chip  dark label small  :color="row.Type == 'case vs.control group' ? 'pink' : 'teal'" >
              {{row.Type == "case vs.control group" ? "Case vs.Control" : "Porfiling" }}
            </v-chip>
          </template>
        </vxe-column>
        <vxe-column field="Trait" min-width="150" sortable title="Trait">
          <template #default="{row}" >


<!--            <v-chip  dark label small  :color="$commonfunc.getColorCodeByTraitType(row.Trait_type)" >{{row.Trait}}</v-chip>-->


            <v-tooltip   top   >
              <template v-slot:activator="{ on, attrs }">
                            <span v-bind="attrs"
                                  v-on="on">

                              <v-chip :color="$commonfunc.getColorCodeByTraitType(row.Trait_type)"  class="text-break  ma-1" label small dark
                                      @click="$commonfunc.openAtNewPageTraitDetail(row.Trait_onto_ID)"
                              > {{row.Trait_Ontology}}</v-chip>
                            </span>
              </template>
              <div class="my-1 mx-2" >
                <b>Trait type:</b><br/>{{row.Trait_type}}
              </div>
            </v-tooltip>



          </template>

        </vxe-column>
<!--        <vxe-column field="Trait_type" width="200" title="Trait Type">-->

<!--        </vxe-column>-->
        <vxe-column field="Condition"  title="Condition" width="180" ></vxe-column>
        <vxe-column field="Tissue" sortable  min-width="50" title="Tissue"></vxe-column>
        <vxe-column field="Cancer_type" min-width="60" sortable title="Cancer Type"></vxe-column>
        <vxe-column :visible="false" field="Cancer_subtype" min-width="60" title="Cancer Subtype"></vxe-column>
        <vxe-column title="Case Infomation"  min-width="100">
          <template #default="{ row }">
            <b>Name</b>: {{ row.Case_name }}
            <b>Age</b>: {{ row.Case_age_group }}
            <b>Sex</b>: <sex-chip :sex="row.Case_sex"  ></sex-chip><br>
            <b>Size</b>: {{ row.Case_size }}
          </template>
        </vxe-column>
        <vxe-column title="Case Concentration"  min-width="50">
          <template #header="{ column }">

                        <span >
                        {{column.title}}</span> <span> <help-msg >
                          <div class="text-body-2">Mean(low-high)/Mean(±SD)</div>
                      </help-msg>
                      </span>


          </template>
          <template #default="{ row }">
            <div class="text-center">
              {{ row.case_concentration && Number(row.case_concentration).toFixed(3) || "NA" }}
              <span v-if="row.case_concentration_low != null || row.case_concentration_high != null" >({{ row.case_concentration_low || "?" }}-{{ row.case_concentration_high || "?" }})</span>
              <span v-else-if="row.case_confidence_interval != null" >( ±{{row.case_confidence_interval}} )</span>
            </div>
          </template>
        </vxe-column>
        <vxe-column title="Control Infomation" min-width="100">
          <template #default="{ row }">
            <b>Name</b>: {{ row.Control_name }}
            <b>Age</b>: {{ row.Control_age_group }}
            <b>Sex</b>: <sex-chip :sex="row.Control_sex" ></sex-chip><br/>
            <b>Size</b>: {{ row.Control_size }}
          </template>

        </vxe-column>
        <vxe-column field="control_concentration" title="Control Concentration"  min-width="50">
          <template #header="{ column }">
                      <span>
                        {{column.title}}</span> <span>
                        <help-msg>
                         <div class="text-body-2">Mean(low-high)/Mean(±SD)</div>
                      </help-msg>
                      </span>
          </template>
          <template #default="{ row }">
            <div class="text-center">
              {{ row.control_concentration && Number(row.control_concentration).toFixed(3) || "NA" }}
              <span v-if="row.control_concentration_low != null || row.control_concentration_high != null" >({{ row.control_concentration_low || "?" }}-{{ row.control_concentration_high || "?" }})</span>
              <span v-else-if="row.control_confidence_interval != null" >( ±{{row.control_confidence_interval}} )</span>
            </div>
          </template>
        </vxe-column>
        <vxe-column sortable field="Platform" title="Platform"  min-width="100"></vxe-column>
        <vxe-column :visible="false" field="concentration_unit" title="Unit"  min-width="50"></vxe-column>
        <vxe-column field="Delta_trend"  min-width="100"  :export-method="$commonfunc.exportFuncDeltaTrends"   title="Delta Trend">
          <template #default="{ row }">
            <v-chip :color="row.Delta_trend && (row.Delta_trend.toUpperCase() =='DOWN'  ? 'teal':( row.Delta_trend.toUpperCase() == 'UP'? 'pink' :'grey' ) )  || 'grey' " dark label
                    small> {{row.Delta_trend && row.Delta_trend.toLowerCase() || "NA" }}
            </v-chip>
          </template>

        </vxe-column>
        <vxe-column field="case_control_p-value" width="150" title="P Value">

          <template #default="{row}">

            <v-chip small label dark class="text-body-2" :color="pvalueColor(row.p_for_sort)" > {{row['case_control_p-value']  || 'NA'  }}</v-chip>
          </template>

        </vxe-column>
        <vxe-column field="Conclusion" title="Conclusion"  min-width="200">

          <template #default ="{row}">

            {{$commonfunc.capitalize(row.Conclusion)}}

          </template>

        </vxe-column>

        <vxe-column field="case_control_q"   min-width="50" :visible="false" title="Q Value">
        </vxe-column>


        <vxe-column field="mz"   min-width="100" :visible="false" title="mz">
        </vxe-column>


        <vxe-column field="peak_position"   min-width="100" :visible="false" title="Peak Position">
        </vxe-column>

        <vxe-column field="VIP"  min-width="100" :visible="false" title="VIP">
        </vxe-column>

        <vxe-column field="RTs"  min-width="100" :visible="false" title="RT">
        </vxe-column>

        <vxe-column field="Molecular_Formula"  min-width="100" :visible="false" title="Molecular Formula">
        </vxe-column>

        <vxe-column field="FC"   min-width="100" :visible="false" title="Fold Change">
        </vxe-column>

        <vxe-column field="log2FC"   min-width="100" :visible="false" title="Log2 Fold Change">
        </vxe-column>


        <vxe-column field="AUC"  :visible="false" title="AUC">
        </vxe-column>

        <vxe-column field="AUROC"  :visible="false" title="ROC">
        </vxe-column>

        <vxe-column field="Adduct"  :visible="false" title="Adduct">
        </vxe-column>
        <vxe-column field="sensitivity"  :visible="false" title="Sensitivity">
        </vxe-column>
        <vxe-column field="specificity"  :visible="false" title="Specificity">
        </vxe-column>


      </vxe-table>
      <vxe-pager
        :current-page="tablePage1.currentPage"
        :layouts="['PrevPage', 'JumpNumber', 'NextPage', 'FullJump', 'Sizes', 'Total']"
        :loading="tableloading1"
        :page-size="tablePage1.pageSize"
        :total="dataLength"
        @page-change="handlePageChange1">
      </vxe-pager>
    </v-sheet>

  </v-sheet>
</template>

<script>
import Axios from "axios";
import HelpMsg from "../sub/helpMsg";
import PercentBar3Item from "../sub/percentBarItems";
import PercentBarItemsPval from "../sub/percentBarItemsPval";
import SexChip from "../sub/sexChip";
import DeltaTrendSummary from "../tableLegends/LegendDeltaTrendSummary";
import PvalSummary from "../tableLegends/LegendPvalSummary";
import LegendSex from "../tableLegends/LegendSex";
import OpenExternalLink from "../tableLegends/LegendOpenExternalLink";
import TraitType from "../tableLegends/LegendTraitType";
import LegendCategory from "../tableLegends/LegendCategory";
import _ from 'lodash'


export default {
  name: "pageSubStudyAssociation",
  components: {
    LegendCategory,
    TraitType,
    OpenExternalLink,
    LegendSex,PvalSummary, DeltaTrendSummary, SexChip, PercentBarItemsPval, PercentBar3Item, HelpMsg},
  props:["cid"],
  data() {
    return {
      allAlign: null,
      tableData:[],
      dataLength:0,
      tableloading1: false,
      pagedData:[],
      tablePage1: {
        currentPage: 1,
        pageSize: 10,
      },
    }
  },
  created() {
    this.$nextTick(() => {
      this.$refs.xTable1S.connect(this.$refs.xToolbar1S)
    })


    let url = "/api/get_study_by_cid"

    this.tableloading1= true;
    Axios.post(
      baseURL+url,{},{params: {cid: this.cid}}
    ).then(res=>{
      this.tableData = res.data
      this.dataLength = res.data.length;
      this.tableloading1 =false;
    })
  },
  computed:{
    tableDataWithPage(){
      return{
        data:this.tableData,
        currentPage:this.tablePage1.currentPage,
        pageSize:this.tablePage1.pageSize
      }
    }
  },
  watch:{
    tableDataWithPage(newval,oldval){
      if (newval.data != null && newval.currentPage !=null,newval.pageSize != null){
        let start = (this.tablePage1.currentPage -1)* this.tablePage1.pageSize;
        let end = start + this.tablePage1.pageSize
        this.pagedData= this.tableData.slice(start,end)
      }
    }
  },
  methods:{
    handlePageChange1({currentPage, pageSize}) {
      this.tablePage1.currentPage = currentPage
      this.tablePage1.pageSize = pageSize
      let start = (this.tablePage1.currentPage -1)* this.tablePage1.pageSize;
      let end = start + this.tablePage1.pageSize
      this.pagedData= this.tableData.slice(start,end)


    },
    goOuterLinkPubmed(cid) {
      window.open('https://pubmed.ncbi.nlm.nih.gov/' + cid, '_blank')
    },
    pvalueColor(p){

      if (p == null){
        return "grey"
      }

      if(  parseFloat(p) <0.01){
        return 'pink'
      }else if(parseFloat(p) >=0.01 && parseFloat(p) <0.05){
        return 'teal'
      }else if (parseFloat(p) >=0.05){
        return 'yellow darken-3'
      }
    }
  }


}
</script>

<style scoped>

</style>



