<template>
  <v-sheet>
    <v-row>

      <v-col>
        <v-sheet class="text-left my-6 text-h5 text-justify">
          <div :color="$store.state.mainColor">
            Introduction
          </div>

        </v-sheet>
        <v-sheet class="text-justify my-6">


          The tool below can be used to map
          metabolite names to PubChem identifiers(PubChem IDs), which plays an important role in standardizing
          metabolite name in
          MACdb. This tool works by accessing PubChem's PUG API through <a
          href="https://pubchempy.readthedocs.io/en/latest/index.html" style="text-decoration: none"
          target="_blank">pubchempy</a>
          . For PubChem has restrictions on the
          frequency of API access, the tool ensures the accuracy and timeliness of the conversion by setting up the task
          queue. If you upload a large number of metabolites, or multiple users using the tool at the same time, you may
          need to wait a short while. You can view the annotation progress at any time through the Task ID.

        </v-sheet>

        <v-sheet class="pa-4" outlined rounded>
          <v-row>
            <v-spacer></v-spacer>
            <v-col class="pt-4" cols="3">
              <div class="text-body-1 font-weight-bold">Original Metabolite Names</div>
              <v-sheet class="text-left  " height="40" >
                <v-row>
                  <v-col cols="1">

                <a class="my-auto" style="text-decoration: none ;" @click="showExample">#Example</a>

                  </v-col></v-row>
              </v-sheet>
              <v-textarea class="overflow-y-auto pt-2 pb-0"
                          clear-icon="mdi-close-circle"
                          clearable
                          filled
                          hide-details label="Paste metabolite names into here" outlined row-height="10"
                          v-model="originName"
                          rows="15">

              </v-textarea>
            </v-col>
            <!--            <v-col cols="2"></v-col>-->
            <v-col class="pt-4" cols="9">
              <v-sheet  >

              <div class="text-body-1 font-weight-bold">Mapped PubChem Compound IDs
                <help-msg>Other information of the compounds are also shown here.</help-msg>
              </div>
              <v-sheet class="text-right " height="40">
                <v-row>
                  <v-col cols="1">
                    <div class="text-left text-body-2" ><help-msg >Only first 100 rows are shown, if you have uploaded more than 100 metabolites please download full mapping results. </help-msg></div>
                  </v-col>
                  <v-spacer></v-spacer>
                  <v-col cols="4" >
                    <v-btn  small  elevation="0" :disabled="taskStatus != 'ok'" outlined  @click="copyResults" :color="$store.state.mainColor">Copy</v-btn> &nbsp;

                    <v-btn :color="$store.state.mainColor"  small  elevation="0"  outlined  :disabled="taskStatus != 'ok'"  @click="download" >Download</v-btn>
                  </v-col>
                </v-row>



              </v-sheet>
              </v-sheet>
              <v-textarea class="overflow-y-auto pt-2  pb-0" filled hide-details
                          label="Mapped PubChem IDs will be shown here (first 100 rows for preview)" outlined
                          v-model="mappedData"
                          readonly row-height="10" rows="15">

              </v-textarea>
            </v-col>

            <v-spacer></v-spacer>
          </v-row>
          <v-row>
            <v-spacer></v-spacer>
            <v-col cols="4">
              <v-sheet height="5=30">
                    <div >  <span><v-icon v-if="check_loading" >mdi-timer-sand mdi-spin</v-icon></span>    {{msg}}&nbsp;&nbsp; <a v-if="showCopyBotton" style="text-decoration: none" @click="copyTaskID">copy</a> </div>
              </v-sheet>
            </v-col>
            <v-spacer></v-spacer>
          </v-row>
          <v-row>
            <v-spacer></v-spacer>
            <v-col cols="9">
              <v-row align="center" class="py-4">
                <v-col cosl="4">
                  <v-row>
                    <v-spacer></v-spacer>
                    <v-col  cols="12" >
                      <v-btn :color="$store.state.mainColor" :disabled="check_loading" :loading="upload_loading" class="mx-2 white--text"   @click="upload" >
                        <v-icon>mdi-rocket-outline</v-icon>&nbsp; Run
                      </v-btn>

                      <v-btn :color="$store.state.mainColor" class="mx-2 white--text"  @click="reset" >
                        <v-icon>mdi-refresh</v-icon> &nbsp;Reset
                      </v-btn>

                    </v-col>
                  </v-row>


                </v-col>


                <v-divider inset vertical></v-divider>
                <v-col cols="8">
                  <v-row align="center">
                    <v-col class="pr-0" cols="9">
                      <v-text-field  :disabled="check_loading"  clearable dense hide-details outlined placeholder="Task id" v-model="taskid" solo></v-text-field>
                    </v-col>
                    <v-col class="pl-0" cols="3">
                      <v-btn :color="$store.state.mainColor":loading="check_loading" @click="checkTaskInterval" class="mx-2 white--text" :disabled="taskid == null || (taskid && taskid.trim() =='') " >
                        <v-icon dark>mdi-lightbulb</v-icon>&nbsp; Check
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>


            </v-col>
            <v-spacer></v-spacer>
          </v-row>
        </v-sheet>
      </v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import HelpMsg from "./sub/helpMsg";
import Axios from 'axios'
import FileDownload from 'js-file-download'

export default {
  name: "toolsName2ID",
  components: {HelpMsg},
  data() {
    return {
      isRunDemo:false,
      taskStatus:null,
      checkTaskFlag:null,
      checnTaskLeftTimeCount:5,
      taskid:null,
      originName:null,
      mappedData:null,
      upload_loading:false,
      check_loading:false,
      msg:"",
      showCopyBotton:false,
      example:"Isoleucine\n" +
        "Glutamine\n" +
        "Glutamate\n" +
        "Creatine\n" +
        "myoinositol\n" +
        "Alanine\n" +
        "PC ae C40:6\n" +
        "lysoPC a C26:0\n" +
        "PC ae C40:2\n"

    }
  },
  beforeDestroy() {
  window.clearInterval(this.checkTaskFlag)
    },

  watch:{
    "originName":{
      handler:function(newval){
        if (this.originName != null){
          if(this.originName != this.example){

            this.isRunDemo = false
          }

        }

      },
      deep:true,
      immediate:true,
    }

  },
  methods: {
    showExample(){
      this.originName = this.example
      this.isRunDemo = true
    },

    checkTaskInterval(){

      if(this.taskid != null && this.taskid && this.taskid.trim() !=""){
        this.check_loading = true;
        this.setMessage("Checking Task: " + this.taskid +" status...")
        this.checkTask();
        this.checkTaskFlag = window.setInterval(this.runCheck,1000);
      }


    },
    runCheck(){

      this.checnTaskLeftTimeCount = this.checnTaskLeftTimeCount -1
      if (this.checnTaskLeftTimeCount <= 0){
        if (this.taskStatus == "running" || this.taskStatus == null){
          // if (this.taskStatus == "running"){
          //
          // }
          this.checkTask();
        }else{

          this.check_loading = false
          window.clearInterval(this.checkTaskFlag);

          if (this.taskStatus == "ok"){

            this.originName = null;
            this.setMessage("Task: " + this.taskid +" is done.")
            Axios.get(baseURL+"/api/tool_name2id_get_res_string",{
              params:{
                taskid:this.taskid
              }
            }).then(res2=>{
              this.mappedData = res2.data.join("")
            })
          }

        }
        this.checnTaskLeftTimeCount = 5
      }
    },
    checkTask() {
      //单次，check 结果
      console.log("checking...status")
      if(this.taskid != null){


        Axios.get(
          baseURL + "/api/tool_name2id_check_task_status",
          {params:{
              taskid:this.taskid.trim()
            }}
        ).then(res=>{

          this.taskStatus = res.data.status;
          console.log("taskStatus:"+this.taskStatus)
          if (this.taskStatus == "no_exists"){
            this.setMessage("Task: " + this.taskid +" not exists!")
            this.check_loading = false
          }else if(this.taskStatus == "running"){
            this.setMessage("Task: " + this.taskid +" is running, please wait...")
          }
          else if(this.taskStatus=="error"){
            this.setMessage("Task: " + this.taskid +" has some errors, please re-submit your data later or contact us.")
            this.check_loading = false
          }else if(this.taskStatus=="ok"){


            //成功或取得数据后清楚定时
            this.check_loading = false
          }
        })
      }
    },
    upload(){




      if(this.originName != null && this.originName.trim() != "") {
        this.setMessage("")
        this.mappedData = null
        this.taskStatus = null
        this.upload_loading=true
        let cleanstr = this.originName.replace(/\n/g,"%@&@%")
        console.log( cleanstr)
        let formData = new FormData();
        formData.append("nameFile", this.originName)
        Axios.post(
          baseURL + "/api/tool_name2id_mapping",
          {},
          {
            params:{
              nameString: cleanstr
            }
          }
        ).then(res => {
          this.upload_loading=false
          this.taskid= res.data
          this.setMessage("Your data has been submitted successfully with Task ID:" +this.taskid,true)
          if(this.isRunDemo ==true){
            this.checkTaskInterval();
          }
        })

      }


    },
    download(){

      Axios.get(
        "https://ngdc.cncb.ac.cn/macdb/api/tool_name2id_get_res_file?taskid="+this.taskid
      ).then(res=>{

        FileDownload(res.data,this.taskid+".mapped.txt")

      })


    },
    setMessage(t,showCopy=false){
      this.msg = t
      if (showCopy){
        this.showCopyBotton = true
      }else{
        this.showCopyBotton = false
      }
    },
    reset(){
        this.taskStatus=null
        this.taskid=null
        this.originName=null
        this.mappedData=null
        this.upload_loading=false
        this.check_loading=false
        this.setMessage("")
        window.clearInterval(this.checkTaskFlag)
      this.isRunDemo = false;
    },
    copyTaskID(){

      this.$copyText(this.taskid).then(function (e) {
        alert('Copied')
      }, function (e) {
        alert('Can not copy')
      }
      )
    },
    copyResults(){
      this.$copyText(this.mappedData).then(function (e) {
          alert('Copied')
        }, function (e) {
          alert('Can not copy')
        }
      )
    }
  }
}
</script>

<style scoped>

</style>



